<template>
  <div class="content">
    <div class="aside">玩法技巧</div>
    <div class="content-right">
      <div class="title">
        当前位置*玩法技巧
      </div>
      <div class="inside">
        <list-item :list="list">
        </list-item>
        <div class="page">
          <el-pagination @size-change="handleSizeChange"
                         @current-change="handleCurrentChange"
                         :current-page.sync="searchForm.pageNum"
                         :page-sizes="[10, 20, 30, 40]"
                         :page-size="100"
                         layout="sizes, prev, pager, next"
                         :total="total">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>

</template>
<script >
import listItem from '@/components/news/ListItem.vue'
export default {
  data () {
    return {
      searchForm: {
        pageNum: 1,
        pageSize: 10,
        // total: 100
      },
      list: [
        {
          title: '测试标题',
          createdTime: '1568214274739'
        }
      ],
      activeName: ''
    }
  },
  methods: {
    handleSizeChange (val) {
      this.searchForm.pageSize = val
    },
    handleCurrentChange (val) {
      this.searchForm.pageNum = val
    },
    getData (type) {
      let api = '/main/getPlays'
      this.$get(api, {
        params: {
          pageNum: this.searchForm.pageNum - 1,
          pageSize: this.searchForm.pageSize
        }
      }).then(res => {
        this.list = res

      })
    },

  },
  created () {
    this.getData()
  },
  components: {
    listItem
  }
}
</script>



